.page-title{
    text-align: center;
    margin-bottom: 3rem;
    font-size: 3.5rem;
}

.page-subtitle{
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
}

.page-caption{
    text-align: center;
    margin-top: 2.5rem;
    margin-bottom: 1.5rem;
    font-size: 1.08rem;
}

.content-slide{
    margin-top: 0 !important;
    max-width: 235px;
    min-width: 235px;
}

.content-card{
    max-width: 15rem;
    min-width: 240px;
    min-height: 330px;
}

.card-image-container{
    max-height: 116.05px;
    overflow: hidden;
}

.card-text{
    font-size: 0.9rem;
    line-height: 1.05rem;
}

.card-title{
    font-size: 1.2rem;
}

@media (max-width: 768px){
    .content-slide{
        max-width: 220px;
        min-width: 220px;
    }
}